import React, {useEffect, useState} from 'react';
import './ContentFocus.css';
import {makeStyles, ThemeProvider} from '@material-ui/core/styles';
import SvgIcon from "@material-ui/icons/YouTube";
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import axios from "axios";
import {img_300, img_500, img_original, unavailable} from "../config/config";
import Button from "@material-ui/core/Button";
import YouTubeIcon from '@material-ui/icons/YouTube';
import Carousel from "../Carousel/Carousel";

const useStyles = makeStyles((theme) => ({
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    paper: {
        width: '90%',
        maxWidth: '780px',
        height: '95%',
        border: '1px solid #282c34',
        borderRadius: 10,
        backgroundColor: "#39445a",
        color: "white",
        boxShadow: theme.shadows[5],
        padding: theme.spacing(1, 1, 3),
    },
    button: {
        // display: 'block',
        // display: 'flex',
        margin: "0px auto",
        justifySelf: 'center',
        // width: '30em',
        maxWidth: 'calc(40em - 20px)',
        // justifyContent: 'center',
        borderRadius: '10px',
    },
    mySvgStyle: {
        fill: 'red',
        // display: 'inline-flex',
        verticalAlign: 'middle',
        // whiteSpace: 'pre',
        // margin: 'auto 0px',
        // alignItems: 'center',
        // justifySelf: 'center',
    },
}));

export default function ContentFocus({children, id, media_type}) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [movie, setMovie] = useState();
    const [video, setVideo] = useState();

    function getMovie() {
        axios({
            method: "GET",
            url: `https://api.themoviedb.org/3/${media_type}/${id}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`
        }).then(response => {
            setMovie(response.data ?? [])
            // console.log(response.data)
        })
    }


    function getVideo() {
        axios({
            method: "GET",
            url: `https://api.themoviedb.org/3/${media_type}/${id}/videos?api_key=${process.env.REACT_APP_API_KEY}&language=ru-RU`
        }).then(response => {
            setVideo(response.data.results[0]?.key ?? [])
            // console.log(response.data.results[0]?.key)
            //    http://youtu.be/${key}
        })
    }

    useEffect(() => {
        getMovie();
        getVideo();
    }, [])

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <button className="movie" type="button" onClick={handleOpen}>
                {children}
            </button>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    {movie && (
                        <div className="content__wrapper">
                            <div className="content__container">
                                <div className="content__poster">
                                    <img className={'back__poster'}
                                         src={movie.backdrop_path ? `${img_original}/${movie.backdrop_path}` : unavailable}
                                         alt={`${movie.title ? movie.title : (movie.original_title ? movie.original_title : (movie.name ? movie.name : movie.original_name))} Poster`}/>
                                </div>
                                <div className="content__about">
                                 <span className="content__title">
                                     {movie.title ? movie.title : (movie.original_title ? movie.original_title : (movie.name ? movie.name : movie.original_name))}({(movie?.release_date || movie?.first_air_date || '_____').substring(0, 4)})
                                 </span>&emsp;
                                    <a href={`http://youtu.be/${video}`} target={"__blank"}>
                                        <SvgIcon className={classes.mySvgStyle}>
                                            <YouTubeIcon/>
                                        </SvgIcon>
                                    </a>
                                    <Button
                                        size={'large'}
                                        className={classes.button}
                                        // variant={'contained'}
                                        // variant={"raised"}
                                        color={'secondary'}
                                        style={{backgroundColor: 'transparent'}}
                                        target={"__blank"}
                                        href={`http://youtu.be/${video}`}
                                        startIcon={<YouTubeIcon/>}></Button>
                                </div>
                                <div className="content__tag">
                                    {movie.tagline && (<i className={'content__tagline'}>{movie.tagline}</i>)}
                                </div>
                                <div className="content__description">
                                    {movie.overview}
                                </div>
                                <div className="content__carusel">
                                    <Carousel media_type={media_type} id={id}/>
                                </div>
                                <Button
                                    className={classes.button}
                                    variant={'contained'}
                                    color={'primary'}
                                    target={"__blank"}
                                    href={`http://youtu.be/${video}`}
                                    startIcon={<YouTubeIcon/>}>Watch the trailer</Button>
                            </div>
                        </div>)}
                </Fade>
            </Modal>
        </div>
    );
}